
import { Injectable } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatRadioButton } from '@angular/material';
import { FormGroup, FormBuilder, FormArray, Validators, ValidationErrors } from '@angular/forms';
import { DeaValidations } from '../shared/ValidationMessages';
import { ToastrService } from 'ngx-toastr';

const { validationMessages } = DeaValidations;
@Injectable()
export class Common {
    config = {
        timeOut: 6000,
        positionClass: 'toast-top-right',
        preventDuplicates: true,
        tapToDismiss: true,
        progressBar: true,
    };
    constructor(
        private readonly dialog: MatDialog,
        private toastr: ToastrService,
    ) { }
    getFormValidationErrors(form: FormGroup | FormArray, formName: string): string[] {
        const errors: string[] = [];
        Object.keys(form.controls).forEach(key => {
            const controlErrors: ValidationErrors = form.get(key).errors;
            if (controlErrors != null) {
                Object.keys(controlErrors).forEach(keyError => {
                    if (keyError === 'required') {
                        errors.push(validationMessages[formName][key]['required']);
                    } else if (keyError === 'maxlength') {
                        errors.push(validationMessages[formName][key]['maxlength']);
                    } else if (keyError === 'minlength') {
                        errors.push(validationMessages[formName][key]['minlength']);
                    }
                });
            }
        });
        return errors;
    }
    makeSucessToast(title: string, content: string) {
        this.toastr.success(content, title, this.config);
    }
    makeInfoToast(title: string, content: string) {
        this.toastr.info(content, title, this.config);
    }
    makeWarningToast(title: string, content: string) {
        this.toastr.warning(content, title, this.config);
    }
    makeDangerToast(title: string, content: string) {
        this.toastr.error(content, title, this.config);
    }
}
