import { Injectable } from '@angular/core';
import {
    HttpInterceptor,
    HttpRequest,
    HttpResponse,
    HttpHandler,
    HttpEvent,
    HttpErrorResponse,
} from '@angular/common/http';
import { environment } from '../../environments/environment';

import { Observable, throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import { Common } from '../shared/common';
import { NgxSpinnerService } from 'ngx-spinner';
import { LogService, LogLevel } from '../shared/logging/log.service';

@Injectable()
export class HttpConfigInterceptor implements HttpInterceptor {
    constructor(public commonService: Common,
        private spinner: NgxSpinnerService,
        private logger: LogService) { }
    intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        // const token: string = environment.apiKey;
        this.spinner.show();
        // if (token) {
        //     request = request.clone({ headers: request.headers.set('apiKey', 'Bearer ' + token) });
        // }

        if (!request.headers.has('Content-Type')) {
            request = request.clone({ headers: request.headers.set('Content-Type', 'application/json') });
        }
        // request = request.clone({ params: request.params.set('observe', 'response') });
        request = request.clone({ headers: request.headers.set('Accept', 'application/json') });

        return next.handle(request).pipe(
            map((event: HttpEvent<any>) => {
                if (event instanceof HttpResponse) {
                    console.log('event--->>>', event);

                }
                this.spinner.hide();
                return event;
            }),
            catchError((error: HttpErrorResponse) => {
                this.logger.level = LogLevel.Error;
                this.logger.error(error.message);
                if (error.status === 500) {
                    this.commonService.makeDangerToast('Error in service call', 'Internal Server error occured');
                    return throwError(error);

                } else if (error.status === 404) {
                    this.commonService.makeDangerToast('Error in service call', 'Apinot found.');
                    return throwError(error);
                } else if (error.status === 401) {
                    this.commonService.makeDangerToast('Error in service call', 'API not autharized.Contact admin.');
                    return throwError(error);
                }
                let data = {};
                data = {
                    reason: error && error.error.reason ? error.error.reason : '',
                    status: error.status,
                };
                this.commonService.makeDangerToast('Error in service call', data['reason']);
                return throwError(error);
            }));
    }
}
