import { HttpClient } from '@angular/common/http';
import { LogEntry } from './log.service';
import { Observable, Subject } from 'rxjs';
import { AppSettings } from '../../app.settings';

// ****************************************************
// Log Publisher Abstract Class
// NOTE: This class must be located BEFORE
//       all those that extend this class
// ****************************************************
export abstract class LogPublisher {
    location: string;
    abstract log(record: LogEntry): Observable<boolean>;
    abstract clear(): Observable<boolean>;
}

// ****************************************************
// Console Logging Class
// ****************************************************
export class LogConsole extends LogPublisher {
    subject = new Subject<boolean>();
    log(entry: LogEntry): Observable<boolean> {
        // Log to console
        console.log(entry.buildLogString());
        this.subject.next(true);
        return this.subject.asObservable();
    }

    clear(): Observable<boolean> {
        console.clear();
        this.subject.next(true);
        return this.subject.asObservable();
    }
}

// ****************************************************
// Local Storage Logging Class
// ****************************************************
export class LogLocalStorage extends LogPublisher {
    subject = new Subject<boolean>();
    constructor() {
        // Must call super() from derived classes
        super();
        // Set location
        this.location = 'logging';
    }

    // Append log entry to local storage
    log(entry: LogEntry): Observable<boolean> {
        let ret = false;
        let values: LogEntry[];

        try {
            // Retrieve previous values from local storage
            values = JSON.parse(localStorage.getItem(this.location)) || [];
            // Add new log entry to array
            values.push(entry);
            // Store array into local storage
            localStorage.setItem(this.location, JSON.stringify(values));

            // Set return value
            ret = true;
        } catch (ex) {
            // Display error in console
            console.log(ex);
        }

        this.subject.next(true);
        return this.subject.asObservable();
    }

    // Clear all log entries from local storage
    clear(): Observable<boolean> {
        localStorage.removeItem(this.location);
        this.subject.next(true);
        return this.subject.asObservable();
    }
}

// ****************************************************
// Logging Web API Class
// ****************************************************
export class LogWebApi extends LogPublisher {
    subject = new Subject<boolean>();
    constructor(private http: HttpClient) {
        // Must call super() from derived classes
        super();
        // Set location
        this.location = AppSettings.LogUrl;
    }

    // **************
    // Public Methods
    // **************

    // Add log entry to back end data store
    log(entry: LogEntry): Observable<boolean> {
        this.http.post(this.location, entry).subscribe(data => {
            if (data === true) {
                this.subject.next(true);
            } else {
                this.subject.next(false);
            }
        });
        return this.subject.asObservable();
    }

    // Clear all log entries from local storage
    clear(): Observable<boolean> {
        // TODO: Call Web API to clear all values
        this.subject.next(true);
        return this.subject.asObservable();
    }

    // ***************
    // Private Methods
    // ***************
    private handleErrors(error: any): Observable<any> {
        const errors: string[] = [];
        let msg = '';

        msg = 'Status: ' + error.status;
        msg += ' - Status Text: ' + error.statusText;
        if (error.json()) {
            msg += ' - Exception Message: ' + error.json().exceptionMessage;
        }
        errors.push(msg);

        console.error('An error occurred', errors);

        return Observable.throw(errors);
    }
}
