import { User } from '../models/usermodel';
import { Injectable } from '@angular/core';

@Injectable()
export class AppServices {
    private userId;
    private userName;
    private email;
    private user: User;
    constructor() { }

    setUserId(val) {
        this.userId = val;
    }

    getUserId() {
        return this.userId;
    }

    setUserName(val) {
        this.userName = val;
    }

    getUserName() {
        return this.userName;
    }
    setUserEmail(val) {
        this.email = val;
    }

    getUserEmail() {
        return this.email;
    }

    getUsers(): User {
        return this.user;
    }

    setUsers(userData) {
        this.user = userData;
    }
}
