import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Common } from '../../../shared/common';
import { AuthService } from '../../../service/authservice';
import { AppServices } from '../../../service/app.service';
import { User } from '../../../models/usermodel';

@Component({
  selector: 'ngx-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
})
export class LoginComponent implements OnInit {
  public loginForm: FormGroup;
  public isSubmitted = false;
  errors: string[] = [];
  user: User;
  constructor(private router: Router
    , private formBuilder: FormBuilder
    , private readonly common: Common
    , private authService: AuthService
    , private appService: AppServices) { }

  ngOnInit() {
    this.loginForm = this.formBuilder.group({
      userid: ['', Validators.required],
      password: ['', Validators.required],
    });
    this.loginForm.statusChanges.subscribe(status => {
      this.ResetErrorMessages();
      this.errors = this.common.getFormValidationErrors(this.loginForm, 'loginForm');
    });
  }
  get f() { return this.loginForm.controls; }

  login() {
    this.isSubmitted = true;

    // stop here if form is invalid
    if (this.loginForm.invalid) {
      this.ResetErrorMessages();
      this.errors = this.common.getFormValidationErrors(this.loginForm, 'loginForm');
      return;
    }
    this.authService.login(this.loginForm.controls['userid'].value, this.loginForm.controls['password'].value)
      .subscribe((res) => {
        if (res != null) {
          this.user = res;
          sessionStorage.setItem('userId', this.user.userId);
          sessionStorage.setItem('userName', this.user.firstName);
          this.appService.setUserId(this.user.userId);
          this.appService.setUserName(this.user.firstName);
          this.appService.setUsers(this.user);
          this.router.navigate(['admin']);
        } else {
          this.common.makeDangerToast('Error', 'Unable to login Please check login details.');
        }
      });
  }
  ResetErrorMessages() {
    this.errors.length = 0;
  }
}
